export default function createPrecipitationCard(precip) {
  const card = document.createElement("div");
  card.className = "card precipitation-card";

  const precipitationHeader = document.createElement("div");
  precipitationHeader.className = "precipitation-header header";

  const precipitationIcon = document.createElement("span");
  precipitationIcon.className = "material-symbols-outlined icon";
  precipitationIcon.textContent = "humidity_high";

  const precipitationTitle = document.createElement("div");
  precipitationTitle.className = "precipitation-title";
  precipitationTitle.textContent = "ОСАДКИ";

  precipitationHeader.appendChild(precipitationIcon);
  precipitationHeader.appendChild(precipitationTitle);

  const precipitationValue = document.createElement("div");
  precipitationValue.className = "precipitation-value";
  precipitationValue.textContent = `${precip ?? 0} мм`;

  card.appendChild(precipitationHeader);
  card.appendChild(precipitationValue);

  return card;
}
